figure[data-type=image].wy-figure-large {
  width: 100%;
  clear: both; }

figure[data-type=image].wy-figure-medium {
  width: 50%; }

figure[data-type=image].wy-figure-small {
  width: 33%; }

figure[data-type=quote] {
  font-family: "Georgia", serif;
  margin-bottom: 24px;
  margin-left: 24px;
  font-style: italic;
  position: relative;
  border-left: solid 5px #404040;
  padding-left: 24px; }

figure[data-type=quote] figcaption {
  font-weight: bold;
  text-align: left; }

figure[data-type=quote].wy-figure-medium {
  font-size: 20px; }

figure[data-type=quote].wy-figure-large {
  font-size: 24px; }

figure[data-type=quote].wy-figure-right {
  width: 33%; }

figure[data-type=quote].wy-figure-left {
  width: 33%;
  border-left: none;
  border-right: solid 5px #404040;
  padding-left: 0;
  padding-right: 24px;
  margin-left: 0;
  margin-right: 24px; }

figure[data-type=quote] cite {
  display: block;
  text-align: left;
  font-weight: bold; }

figure[data-type=quote] cite:before {
  content: "\2014\00a0"; }

.wy-figure-right {
  float: right;
  margin-left: 24px; }

.wy-figure-right .wy-figure-controls {
  right: 0; }

.wy-figure-left {
  float: left;
  margin-right: 24px; }

@media screen and (max-width: 480px) {
  figure[data-type=image] {
    width: 100% !important;
    float: none !important;
    margin-left: 0;
    margin-right: 0; }
  figure[data-type=video] iframe {
    width: 100% !important;
    height: auto !important; } }

body:before {
  display: none; }

body.grid:before {
  display: block; }

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem; }

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute; }
